html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed,
figure, figcaption, footer, header, hgroup,
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  font: inherit;
  vertical-align: baseline;
  text-decoration: none;
}

/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure,
footer, header, hgroup, menu, nav, section {
  display: block;
}

body {
  line-height: 1;
}

ol, ul {
  list-style: none;
}

blockquote, q {
  quotes: none;
}

blockquote:before, blockquote:after,
q:before, q:after {
  content: "";
  content: none;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

* {
  box-sizing: border-box;
}

@font-face {
  font-family: "Nantes-Bold";
  src: url("../../fonts/Nantes-Bold.woff2") format("woff2");
}
@font-face {
  font-family: "Nantes-Regular";
  src: url("../../fonts/Nantes-Regular.woff2") format("woff2");
}
@font-face {
  font-family: "Nantes-Light";
  src: url("../../fonts/Nantes-Light.woff2") format("woff2");
}
@font-face {
  font-family: "CalibreRegular";
  src: url("../../fonts/CalibreRegular.otf") format("opentype");
}
@font-face {
  font-family: "CalibreMedium";
  src: url("../../fonts/CalibreMedium.otf") format("opentype");
}
html {
  scroll-behavior: smooth;
}

img {
  max-width: 100%;
}

header {
  position: fixed;
  width: 100%;
  height: auto;
  top: 0;
  left: 0;
  padding: 0.86vw 0 0.86vw 0;
  z-index: 9999;
  background-color: #fff;
}
header .content-wrap {
  position: relative;
  padding: 0 6vw 0 6vw;
  display: grid;
  grid-template-columns: 1fr 2fr 1fr;
  align-items: center;
  justify-content: space-between;
  margin: 0 auto;
}
header .content-wrap ul {
  display: flex;
  align-items: center;
}
header .content-wrap li {
  margin-right: 2.34vw;
  line-height: 1.25rem;
}
header .content-wrap li:last-child {
  margin-right: 0px !important;
}
header .content-wrap li p {
  font-size: 1.1em;
  font-weight: 300;
  color: #222226;
  font-family: "CalibreRegular";
}
header .content-wrap li p:hover {
  text-decoration: underline;
  text-underline-position: under;
}
header .content-wrap .header-logo {
  height: auto;
  max-width: 70px;
  place-self: center;
}
header .content-wrap .search {
  justify-self: end;
}
header .content-wrap svg {
  width: 55px;
  height: 25px;
}

.hero-wrapper {
  width: 100%;
  display: flex;
}

.left-column {
  position: relative;
  width: 50%;
  float: left;
}
.left-column img {
  display: block;
  margin: 0;
  width: 100%;
  height: 100vh;
}

.right-column {
  position: sticky;
  float: right;
  top: 0;
  right: 0;
  width: 50%;
  height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;
  padding: 0 6vw 0 6vw;
}
.right-column h1 {
  font-family: "Nantes-Bold";
  font-weight: 700;
  line-height: 3.5rem;
  font-size: 2.8em;
  margin-bottom: 2rem;
}
.right-column h6 {
  font-family: "CalibreRegular";
  font-size: 13px;
}
.right-column a {
  font-family: "CalibreMedium";
  color: #ff6429;
  text-decoration: underline;
}

.text-pannel {
  position: relative;
  width: 100%;
  height: auto;
  background-color: #efcb56;
  text-align: center;
}
.text-pannel .content-wrapper {
  padding: 8.6vw 18.5vw 6.65vw 18.5vw;
}
.text-pannel .content-wrapper h2 {
  font-family: "Nantes-Bold";
  line-height: 3.5rem;
  font-size: 2.8em;
  margin-bottom: 1rem;
}
.text-pannel .content-wrapper h3 {
  font-family: "Nantes-Light";
  line-height: 2.4rem;
  font-size: 2em;
  margin-bottom: 2rem;
}